import md5 from "@/common/SDK/md5.min.js"

export default {
	data() {
		return {
			username: '',
			password:'',
			isShowOauth:false,
			showProvider:{
				weixin:false,
				qq:false,
				sinaweibo:false,
				xiaomi:false
			}
		}
	},
	onShow() {
		
	},
	onLoad() {
		//APP显示第三方登录



		this.getProvider()
		
	}, 
	methods: {
		oauthLogin(provider){
			uni.showLoading();
			//第三方登录
			uni.login({
				provider: provider,
				success: (loginRes)=>{
					console.log("success: "+JSON.stringify(loginRes));
					//案例直接获取用户信息，一般不是在APP端直接获取用户信息，比如微信，获取一个code，传递给后端，后端再去请求微信服务器获取用户信息
					uni.getUserInfo({
						provider: provider,
						success: (infoRes)=>{
							console.log('用户信息：' + JSON.stringify(infoRes.userInfo));
							uni.setStorage({
								key: 'UserInfo',
								data: {
									username:infoRes.userInfo.nickName,
									face:infoRes.userInfo.avatarUrl,
									signature:'个性签名',
									integral:0,
									balance:0,
									envelope:0
								},
								success: function () {
									uni.hideLoading()
									uni.showToast({title: '登录成功',icon:"success"});
									setTimeout(function(){
										uni.navigateBack();
									},300)
								}
							});
						}
					});
				},
				fail:(e)=>{
					console.log("fail: "+JSON.stringify(e));
				}
			});
		},
		getProvider(){
			//获取第三方登录服务
			uni.getProvider({
				service: 'oauth',
				success: (res)=>{
					let len = res.provider.length;
					for(let i=0;i<len;i++){
						//有服务才显示按钮图标
						this.showProvider[res.provider[i]] = true;
					}
					if(res.provider.length==0){
						this.isShowOauth=false;
					}
				}
			});
		},
		toPage(page){
			uni.hideKeyboard()
			uni.navigateTo({
				url: page
			});
		},
		doLogin(){
			let _this = this
			uni.hideKeyboard()
			let username = _this.username,password = _this.password,md5PW = md5(_this.password)
			
			if(username == "" || username.length < 3 || password == "" || password.length < 3){
				_this.is_vuex.dispatch('to_msg','请填写正确的账号密码!')
				return
			}
			_this.is_vuex.dispatch('login_in',{
				username: username,
				password: password
			})
		}
	}
}
